
*, *:after, *:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.cd-testimonials-wrapper {
  position: relative;
  width: 90%;
  background-color: transparent; 
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;  
  font-family: "Helvetica Neue", sans-serif;
  z-index: 1;
}

@media only screen and (min-width: 768px) {
  .cd-testimonials-wrapper {
    /* margin: 4em auto; */
    padding: 0 clamp(1rem,10vw,4rem);
    padding-bottom: 5em;
  }
}

@media only screen and (min-width: 1170px) {
  .cd-testimonials-wrapper::after {    
    display: block;
  }
}  

.cd-testimonials {
  /* color: #2b2e34; */
  /* color: #ffffff; */
  text-align: center;
}

.cd-testimonials::after {
  clear: both;
  content: "";
  display: table;
}

.cd-testimonials > li {
  position: absolute;
  opacity: 0;
}

.cd-testimonials > li:first-child {
  position: relative;
  opacity: 1;
}

.cd-testimonials p {
  font-family: "Georgia", serif;
  /* font-family: "Oswald", "Open sans"; */
  font-style: italic;
  line-height: 1.4;
  color: #2b2e34;
  margin-bottom: 1em;
  padding: 0 14px;
}

@media only screen and (min-width: 768px) {
  .cd-testimonials p {
    font-size: 1.375rem;
  }
}
@media only screen and (min-width: 1170px) {
  .cd-testimonials p {
    font-size: 1.625rem;
    line-height: 1.6;
  }
}

.cd-author .cd-author-info {
  display: inline-block;
  vertical-align: middle;
}

.cd-author .cd-author-info {
  text-align: left;
  line-height: 1.2;
  color: #2b2e34;
}

.cd-author .cd-author-info li { 
  font-size: clamp(0.6rem,1.3vw,1.4rem);
  text-align: center;  
  color: #2b2e34;
}

.cd-author .cd-author-info li:first-child {
  color: #2b2e34;
  font-size: clamp(0.8rem,2.1vw,1.6rem);  
  font-weight: 600;
}

@media only screen and (min-width: 768px) {
  .cd-author .cd-author-info {
    line-height: 1.4;
}

.cd-author .cd-author-info li:first-child {
    font-weight: 600;
  }
}

.flex-direction-nav li {
  position: absolute;
  height: 100%;
  width: 40px;
  top: 0;
}
.flex-direction-nav li:first-child {
  left: 0;
}
.flex-direction-nav li:last-child {
  right: 0;
}

.flex-direction-nav li a {
  display: block;
  height: 100%;
  width: 100%;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  -webkit-transition: background-color 0.2s;
  -moz-transition: background-color 0.2s;
  transition: background-color 0.2s;
}
.no-touch .flex-direction-nav li a:hover {
  background-color: #323234;
}
.flex-direction-nav li a::before, .flex-direction-nav li a::after {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  width: 4px;
  height: 35px; 
  background-color: #ffffff;

}
.flex-direction-nav li a::before {
  -webkit-transform: translateY(-50px) rotate(45deg);
  -moz-transform: translateY(-50px) rotate(45deg);
  -ms-transform: translateY(-50px) rotate(45deg);
  -o-transform: translateY(-50px) rotate(45deg);
  transform: translateY(-50px) rotate(45deg);
}
.flex-direction-nav li a::after {
  -webkit-transform: translateY(-27px) rotate(-45deg);
  -moz-transform: translateY(-27px) rotate(-45deg);
  -ms-transform: translateY(-27px) rotate(-45deg);
  -o-transform: translateY(-27px) rotate(-45deg);
  transform: translateY(-27px) rotate(-45deg);
}
.flex-direction-nav li:last-child a::before {
  -webkit-transform: translateY(-50px) rotate(-45deg);
  -moz-transform: translateY(-50px) rotate(-45deg);
  -ms-transform: translateY(-50px) rotate(-45deg);
  -o-transform: translateY(-50px) rotate(-45deg);
  transform: translateY(-50px) rotate(-45deg);
}
.flex-direction-nav li:last-child a::after {
  -webkit-transform: translateY(-27px) rotate(45deg);
  -moz-transform: translateY(-27px) rotate(45deg);
  -ms-transform: translateY(-27px) rotate(45deg);
  -o-transform: translateY(-27px) rotate(45deg);
  transform: translateY(-27px) rotate(45deg);
}
@media only screen and (min-width: 768px) {
  .flex-direction-nav li {
    width: 50px;
  }
}
@media only screen and (min-width: 1170px) {
  .flex-direction-nav li a::before, .flex-direction-nav li a::after {
    background-color: #8c8c94;
    -webkit-transition: background-color 0.2s;
    -moz-transition: background-color 0.2s;
    transition: background-color 0.2s;
  }
  .flex-direction-nav li a:hover::before, .flex-direction-nav li a:hover::after {
    background-color: #525255;
  }
}

.cd-see-all {
  position: absolute;
  z-index: 1;
  left: 50%;
  bottom: 0;
  transform: translate(-50%); 
  text-transform: uppercase;
  color: #2b2e34;
  font-weight: 700; 
}

.cd-see-all {    
  border: 1px #2b2e34 solid;    
  border-radius: 10px;
}

.cd-see-all span {
  font-size: clamp(0.8rem, 1.7vw, 1.3rem);
}

.cd-see-all span:hover, .cd-see-all span:focus {
  color: #2b2e34;
  font-size: clamp(0.7rem, 1.7vw, 1.28rem);
}

.cd-testimonials-all {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  -webkit-transition: opacity .3s 0s, visibility 0s .3s;
  -moz-transition: opacity .3s 0s, visibility 0s .3s;
  transition: opacity .3s 0s, visibility 0s .3s;
}
.cd-testimonials-all .cd-testimonials-all-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  overflow: auto;
  background-color: #747479;
}
.cd-testimonials-all .cd-testimonials-all-wrapper > ul {
  width:100%;
  max-width: 80%;
  margin: 4.5em auto;
}

.cd-testimonials-all .cd-testimonials-all-wrapper > ul {
       margin: 4.5em auto;
        width:100%;
        max-width: 70%; 
}

@media only screen and (min-width: 500px) {
  .cd-testimonials-all .cd-testimonials-all-wrapper > ul {
   
    max-width: 80%;
    
    }
}
.cd-testimonials-all .cd-testimonials-all-wrapper > ul::after {
  clear: both;
  content: "";
  display: table;
}
.cd-testimonials-all .cd-testimonials-item {
  margin-bottom: 2em;
  opacity: 0;
  /* Force Hardware Acceleration in WebKit */
  -webkit-transform: translate3d(0, 0, 0) scale(0.4);
  -moz-transform: translate3d(0, 0, 0) scale(0.4);
  -ms-transform: translate3d(0, 0, 0) scale(0.4);
  -o-transform: translate3d(0, 0, 0) scale(0.4);
  transform: translate3d(0, 0, 0) scale(0.4);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -webkit-transition: -webkit-transform .3s, opacity .3s;
  -moz-transition: -moz-transform .3s, opacity .3s;
  transition: transform .3s, opacity .3s;
}
.cd-testimonials-all p {
  position: relative;
  background: #2b2e34;
  color: #ffffff;
  padding: 2em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.4;
  border-radius: 0.25em;
  margin-bottom: 1em;
}
.cd-testimonials-all p::after {
  /* creating the triangle in css */
  content: '';
  position: absolute;
  top: 100%;
  left: 2em;
  height: 0;
  width: 0;
  border: 8px solid transparent;
  border-top-color: #2b2e34;
}
.cd-testimonials-all .cd-author {
  text-align: left;
  margin-left: 1.5em;
}
.cd-testimonials-all .cd-author img {
  box-shadow: 0 0 0 transparent;
}
.cd-testimonials-all.is-visible {
  visibility: visible;
  opacity: 1;
  -webkit-transition: opacity .3s 0s, visibility 0s 0s;
  -moz-transition: opacity .3s 0s, visibility 0s 0s;
  transition: opacity .3s 0s, visibility 0s 0s;
}
.cd-testimonials-all.is-visible .cd-testimonials-all-wrapper {
  -webkit-overflow-scrolling: touch;
}
.cd-testimonials-all.is-visible .cd-testimonials-item {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) scale(1);
  -moz-transform: translate3d(0, 0, 0) scale(1);
  -ms-transform: translate3d(0, 0, 0) scale(1);
  -o-transform: translate3d(0, 0, 0) scale(1);
  transform: translate3d(0, 0, 0) scale(1);
}
@media only screen and (min-width: 768px) {
  .cd-testimonials-all .cd-testimonials-item {
    width: 46%;
    margin: 0 2% 3em;
  }
  .cd-testimonials-all .cd-author {
    margin-left: 1em;
  }
}
@media only screen and (min-width: 1170px) {
  .cd-testimonials-all .cd-testimonials-item {
    width: 30%;
    margin: 0 1.6666% 3em;
  }
}

.close-btn {
  position: fixed;
  display: block;
  width: 40px;
  height: 40px;
  top: 4em;
  right: 2%;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
}
.close-btn::before, .close-btn::after {
  content: '';
  position: absolute;
  display: inline-block;
  width: 2px;
  height: 24px;
  top: 18px;
  left: 19px;
  background-color: #39393c;
}
.close-btn::before {
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  -o-transform: rotate(45deg);
  transform: rotate(45deg);
}
.close-btn::after {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
